<template>
  <el-card class="box-card">
    <!-- 头部信息栏 -->
    <el-row :gutter="20" type="flex" justify="center">
      <el-col :span="6">
        <div style="height: 80px; background-color: #2C3E50; color: aliceblue;">
          <span>图片</span>
        </div>
      </el-col>
      <el-col :span="16" class="text-box book-msg">
        <div>
          <span style="font-size: 20px;color: black;">{{thisbook.name ? thisbook.name : '暂无图书信息'}}</span>
        </div>
        <div>
          <span class="small-text">每日 
          {{thisbook.daily_word_num ? thisbook.daily_word_num : 0}}
           词，剩余 
           {{thisbook.rest_day_num ? thisbook.rest_day_num : 0}}
            天</span>
        </div>
        <div style="margin: 4px 0;">
          <el-progress :percentage="(thisbook.learned_word_num / thisbook.total_word_num * 100)" :show-text='false'></el-progress>
        </div>
        <div class="small-text grey-font" style="display: flex;justify-content: space-between">
          <span>已学单词</span>
          <span>{{thisbook.learned_word_num}}/{{thisbook.total_word_num}}</span>
        </div>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <!-- 背诵计划 -->
    <el-row :gutter="20" class="plan-box">
      <el-col :span="12">
        <span class="primary-text" style="font-size: 20px;"> 今日计划</span>
      </el-col>
      <el-col :span="12">
        <span class="primary-text">在此处有推广</span>
      </el-col>
    </el-row>
    <!-- 需学单词 -->
    <el-row :gutter="20" class="plan-box">
      <el-col :span="12">
        <p class="primary-text small-text">需新学</p>
        <p class="letter-text">
          <i class="number-text">{{plan.new_word_num}}</i>
          <span>&nbsp;词</span>
        </p>
      </el-col>
      <el-col :span="12">
        <p class="primary-text small-text">需复习</p>
        <p class="letter-text">
          <i class="number-text">{{plan.review_word_num}}</i>
          <span>&nbsp;词</span>
        </p>
      </el-col>
    </el-row>
    <!-- 按钮 -->
    <div style="margin-top: 20px;">
      <div v-if="show_time_cost" class="small-text grey-font">
        <span class="el-icon-time"></span>
        预计用时 {{plan.cost_time_num}} 分钟
      </div>
      <el-button type="primary" style="width: 90%;margin: 10px 0;font-size: 20px;">
        开始背单词吧！
      </el-button>
    </div>
  </el-card>
  
</template>

<script>
export default {
  name: 'HomeCard',
  props: ['thisbook','plan'],
  data() {
    return{
      show_time_cost: true
    }
  }
}
</script>

<style>
.box-card{
  margin: 15px;
}
.text-box{
  text-align: left;
}
.primary-text{
  margin-left: 10px;
}
.book-msg{
  line-height: 25px;
}
.small-text{
  font-size: 12px;
}
.plan-box{
  text-align: left;
  margin: 0px 0;
}
.number-text{
  font-size: 48px;
  font-weight: bold;
}
.letter-text{
  margin-left: 10px;
}
.grey-font{
  color: darkgrey;
}
</style>
