<template>
  <div>
    <div
    id="ciyi-main"
    :style="{height:(screenHeight*0.8)+'px'}">
      <p :style="{height:(screenHeight*0.4)+'px'}" class="main-font">
        <span class="son-font">fluency</span>
      </p>
      <div class="button-group">
        <el-button class="ciyi-button">prop.&nbsp;词意</el-button>
        <el-button class="ciyi-button">prop.&nbsp;词意</el-button>
        <el-button class="ciyi-button">prop.&nbsp;词意</el-button>
        <el-button class="ciyi-button">prop.&nbsp;词意</el-button>
        
      </div>
    </div>
  </div>
</template>

<script>
export default{
  name: 'XuanCiYi',
  data() {
    return {
      screenHeight: document.documentElement.clientHeight //   屏幕高度
    }
  },
  watch: {
    'screenHeight': function (val) { // 监听屏幕高度变化
      var oIframe = document.getElementById('ciyi-main')
      oIframe.style.height = Number(val) + 'px'
    }
  },
  mounted () {
    var _this = this
    window.onresize = function () { // 定义窗口大小变更通知事件
      _this.screenHeight = document.documentElement.clientHeight // 窗口高度
    }
  }
}
</script>
<style>
.main-font{
  color: #0a39aa;
  height: inherit;
  font-size: 36px;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.son-font{
  height: inherit;
}
.button-group{
  line-height: 60px;
}
.ciyi-button{
  width: 90%;
  text-align: left;
  height: 50px;
}
</style>
